import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function investedCapitalTtm(tableName: string) {
  const title = "Invested Capital (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["investedCapitalTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Capital invested in the business.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about invested capital or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.investedCapitalTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.investedCapitalTtm}
        />
      )
    },
  })
}

export { investedCapitalTtm }
